@import url(https://fonts.googleapis.com/css?family=Heebo:800&display=swap);
#app body {
  background-color: #f8f9fa;
  height: 100%; }

#app .navbar-brand {
  margin: 0 auto;
  font-family: 'Heebo', sans-serif;
  color: #f8f9fa !important;
  font-size: 2em;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.75); }

#app .navbar {
  box-shadow: 0 0 15px #333; }

#app .navbar-light {
  background-color: #0A3F73; }

#app .footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #0A3F73;
  color: #f8f9fa;
  text-align: center;
  line-height: 3em;
  font-weight: 500; }

#app .form-container {
  width: 70%;
  max-width: 700px;
  margin: 20px auto; }

#app .form-label {
  width: 130px; }

#app .form-control {
  width: calc(100% - 130px - 100px) !important;
  margin-right: 10px; }

#app .loader .lds-ring {
  display: inline-block;
  position: relative;
  width: 2rem;
  height: 2rem; }

#app .loader .lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 3rem;
  height: 3rem;
  margin: 6px;
  border: 6px solid #999;
  border-radius: 50%;
  -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #999 transparent transparent transparent; }

#app .loader .lds-ring div:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s; }

#app .loader .lds-ring div:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s; }

#app .loader .lds-ring div:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s; }

@-webkit-keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

#app .center {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%); }

#app .error {
  font-size: 2em;
  color: #f00; }

#app .weather-info {
  width: 70%;
  height: 60%;
  max-width: 400px;
  max-height: 300px;
  border-radius: 20px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  padding: 10px;
  background-color: #DFF2FA; }
  #app .weather-info .city-name {
    font-size: 1.3em;
    margin-top: 10px;
    text-align: center; }
  #app .weather-info .temp {
    font-size: 2em;
    font-weight: 500;
    color: #0A3F73;
    text-align: center; }
  #app .weather-info .description {
    text-align: center; }
  #app .weather-info .icon {
    text-align: center; }
  #app .weather-info .text {
    text-align: center; }
  #app .weather-info .flag {
    display: inline-block; }

